}
.flechaup a {
  background-color: hsla(0deg, 0%, 40%, 0.9);
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  width: 5rem;
  height: 5rem;
  border: 1px solid #585858;
}
.flechaup a i {
  font-size: 2rem;
  color: #e1e1e1;
}
.flechaup a:hover {
  background-color: #2a2a2a;
}

