.dashboard.swal2-height-auto {
  height: 100% !important;
}
.dashboard__grafica {
  width: min(95%, 60rem);
  margin: 0 auto;
}
.dashboard__header {
  position: fixed;
  z-index: 4;
  top: 0;
  width: 100%;
  background-color: #333;
  padding: 1rem 2rem;
}
@media (min-width: 992px) {
  .dashboard__header {
    display: none;
  }
}
.dashboard__header-grid {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.dashboard__logo {
  color: #FFFFFF;
  text-align: center;
  font-weight: 600;
  /* @include tablet{
       text-align: left;
       margin: 0;
   }*/
  font-size: clamp(2rem, -0.8rem + 10vw, 4rem);
}
.dashboard__logo--menul {
  padding-bottom: 2rem;
}
.dashboard__btnmenu {
  padding: 1rem;
  background-color: #000;
}
.dashboard__btnmenu i {
  font-size: 3rem;
  color: #FFFFFF;
}
.dashboard__btnmenu:hover {
  cursor: pointer;
}
@media (min-width: 992px) {
  .dashboard__grid {
    display: flex;
    flex: 1;
    overflow-y: auto;
  }
}
.dashboard__sidebar {
  position: fixed;
  z-index: 4;
  top: 0%;
  width: 0rem;
  height: 43rem;
  transition: all 0.6s ease;
  overflow: hidden;
  background-color: #191919;
}
@media (min-width: 992px) {
  .dashboard__sidebar {
    position: relative;
    height: auto;
    display: block;
    width: 28rem;
  }
}
.dashboard__mostrar_menuL {
  width: 25rem;
  height: 43rem;
}
@media (min-width: 992px) {
  .dashboard__mostrar_menuL {
    height: auto;
  }
}
.dashboard__menu {
  padding: 0 0 0 4rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  gap: 1rem;
  height: 100%;
}
@media (min-width: 992px) {
  .dashboard__menu {
    padding: 0 0 25rem 3rem;
  }
}
@media (min-width: 1440px) {
  .dashboard__menu {
    padding: 0 0 11rem 4rem;
  }
}
.dashboard__enlace {
  position: relative;
  white-space: nowrap;
  color: #FFFFFF;
  font-weight: 400;
  font-size: 1.5rem;
  padding: 1rem 0rem;
  border-radius: 1rem;
}
.dashboard__enlace::after {
  content: "";
  opacity: 0;
}
.dashboard__enlace--actual {
  color: #0cdad3;
}
.dashboard__enlace--actual::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 100%;
  width: 30px;
  height: 2px;
  background-color: #0cdad3;
  margin-left: 1rem;
  opacity: 1;
  transition: opacity 1s ease;
}
.dashboard__enlace:hover {
  color: #0cdad3;
  /* &::after{
       background-color: v.$secundario;
   }*/
}
.dashboard__contenido {
  position: relative;
  width: 100%;
  scroll-behavior: smooth;
}
@media (min-width: 992px) {
  .dashboard__contenido {
    overflow-y: auto;
  }
}
@media (min-width: 768px) {
  .dashboard__contenedor-boton {
    display: flex;
    justify-content: right;
  }
}
.dashboard__contenedor, .dashboard__formulario {
  box-shadow: rgba(0, 0, 0, 0.1) 0px 20px 25px -5px, rgba(0, 0, 0, 0.04) 0px 10px 10px -5px;
  background-color: #FFFFFF;
  padding: 2rem;
  border-radius: 1rem;
}
.dashboard__formulario {
  width: min(95%, 60rem);
  margin: 0 auto;
}

.home {
  position: relative;
  z-index: 3;
  height: 45rem;
  background-image: linear-gradient(to left, rgba(0, 0, 0, 0.2), rgba(42, 42, 42, 0.8)), url(/build/img/home4.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center right;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (min-width: 480px) {
  .home {
    height: 60rem;
  }
}
@media (min-width: 992px) {
  .home {
    height: 100vh;
  }
}
.home__contenedor {
  width: min(95%, 60rem);
  margin: 0 auto;
}
.home__descripcion p, .home__descripcion h1, .home__descripcion a {
  color: #FFFFFF;
}
.home__descripcion p {
  display: flex;
}
.home__descripcion p::after {
  content: "";
  width: 60px;
  height: 1px;
  display: inline-block;
  background-color: #FFFFFF;
  align-self: center;
  margin-left: 1rem;
  transition: margin 0.6s ease;
}
.home__descripcion h1 {
  font-size: clamp(2.4rem, -0.8rem + 10vw, 3.6rem);
  margin: 2rem 0;
}
.home__descripcion:hover p::after {
  margin-left: 4rem;
}
.home__mensaje {
  color: #FFFFFF;
  position: absolute;
  left: 5%;
  bottom: 12%;
}
.home .maquina-escribir {
  display: inline-flex;
  white-space: nowrap;
  border-right: 4px solid #FFFFFF;
  width: 24rem;
  animation: escribir 4s steps(18), blinks 0.5s infinite step-end alternate;
  overflow: hidden;
}
@keyframes escribir {
  from {
    width: 0;
  }
}
@keyframes blinks {
  50% {
    border-color: transparent;
  }
}
.home__btn1 {
  background-color: transparent;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  font-weight: 400;
  border: 1px solid hsla(0deg, 0%, 40%, 0.8);
  border-radius: 5rem;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
  width: auto;
}
.home__btn1:hover {
  background-color: rgba(0, 0, 0, 0);
  cursor: pointer;
}
.home__btn1:hover {
  background-color: #e6e6e6;
  color: #585858;
}
.home__aboutme {
  position: absolute;
  color: #FFFFFF;
  font-size: 2.5rem;
  left: 50%;
  transform: translateX(-50%);
  bottom: 10%;
  animation: aboutme 1.2s ease-out infinite;
}
@keyframes aboutme {
  0% {
    bottom: 10%;
    opacity: 0;
  }
  16% {
    bottom: 9%;
    opacity: 0.1;
  }
  32% {
    bottom: 8%;
    opacity: 0.3;
  }
  100% {
    bottom: 4%;
    opacity: 1;
  }
}
.home svg {
  position: absolute;
  bottom: 0%;
  left: 0%;
  fill: #2a2a2a;
}

.aboutme {
  background-color: #2a2a2a;
  position: relative;
}
.aboutme__contenedor {
  width: min(95%, 120rem);
  margin: 0 auto;
  padding-bottom: 18rem;
}
.aboutme__titulos, .aboutme__titulos--mywork {
  padding: 8rem 0;
  position: relative;
  text-align: center;
}
.aboutme__titulos h1, .aboutme__titulos--mywork h1 {
  color: #373737;
  font-size: clamp(6rem, -0.8rem + 10vw, 12rem);
}
.aboutme__titulos--mywork h1 {
  color: #373737;
  font-size: clamp(5rem, -0.8rem + 10vw, 12rem);
}
.aboutme__titulos p, .aboutme__titulos--mywork p {
  font-size: clamp(3rem, -0.8rem + 10vw, 4rem);
  font-weight: 500;
  color: #FFFFFF;
  position: absolute;
  top: 60%;
  width: 100%;
  left: 50%;
  transform: translateX(-50%);
}
@media (min-width: 768px) {
  .aboutme__titulos p, .aboutme__titulos--mywork p {
    top: 42%;
  }
}
.aboutme__titulos p::after, .aboutme__titulos--mywork p::after {
  margin-top: 1rem;
  display: block;
  content: "";
  width: 80px;
  height: 2px;
  background-color: #0cdad3;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
}
@media (min-width: 992px) {
  .aboutme__personalinformation {
    display: flex;
    gap: 2rem;
  }
}
.aboutme__perfil {
  text-align: center;
}
@media (min-width: 992px) {
  .aboutme__perfil {
    text-align: left;
  }
}
.aboutme__perfiltitulo {
  color: #FFFFFF;
  margin-bottom: 2rem;
}
.aboutme__perfiltitulo span {
  color: #0cdad3;
}
.aboutme__perfildescripcion {
  line-height: 1.8;
  color: #ccc;
  font-size: 1.6rem;
}
.aboutme__personaldata {
  margin-top: 6rem;
  color: #FFFFFF;
}
@media (min-width: 992px) {
  .aboutme__personaldata {
    margin: unset;
  }
}
.aboutme__personaldata p {
  font-size: 1.8rem;
  font-weight: 500;
  white-space: nowrap;
  border-bottom: 1px solid #3e3e3e;
  padding: 2rem 0;
}
.aboutme__personaldata p:nth-of-type(3) {
  border-bottom: none;
}
.aboutme__personaldata p span {
  color: #ccc;
  font-weight: 300;
}
.aboutme__personaldata .home__contbtn {
  margin-top: 3rem;
}
.aboutme__contadores {
  color: #FFFFFF;
  display: flex;
  justify-content: space-around;
  margin-top: 6rem;
  padding-bottom: 1rem;
}
.aboutme__contador {
  text-align: center;
}
.aboutme__contador span {
  font-size: 4.5rem;
  font-weight: 500;
}
.aboutme__contador p {
