  border: 1px solid #FFFFFF;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  animation: rotacion 2s linear infinite;
  animation-play-state: paused;
}
.services__icon::after {
  position: absolute;
  top: 0%;
  transform: translateY(-50%);
  content: " ";
  width: 1.7rem;
  height: 1.7rem;
  clip-path: circle();
  background-color: #a5a5a5;
}
.services__icon::before {
  position: absolute;
  bottom: 0%;
  transform: translateY(50%);
  content: " ";
  width: 1.7rem;
  height: 1.7rem;
  clip-path: circle();
  background-color: #a5a5a5;
}
.services__icon:hover {
  animation-play-state: running;
}
@keyframes rotacion {
  0% {
    transform: rotate(-25deg);
  }
  100% {
    transform: rotate(335deg);
  }
}
